@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Ubuntu', sans-serif;
}

header {
    position: relative;
    width: 100%;
    padding: 30px 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header .logo {
    position: relative;
    color: #05445E;
    ;
    text-decoration: none;
    font-size: 2em;
    font-weight: 700;
}

.group {
    position: relative;
    display: flex;
}

.group .navigation,
.group .action {
    display: flex;
    justify-content: center;
    align-items: center;
}

.group ul li {
    list-style: none;
}

.group ul li a {
    color: #05445E;
    text-decoration: none;
    margin-left: 30px;
}

.group ul li a:hover {
    color: #ff5a26;
}

.group .action {
    margin-left: 20px;
}

.group .action li a {
    font-size: 1.25em;
}

.banner {
    position: relative;
    width: 100%;
    padding: 0 100px;
    transform:translateY(40px);
}

.banner .bg {
    position: relative;
    width: 100%;
    height: 75vh;
    background: rgba(212, 241, 244, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn {
    display: inline-block;
    background: #ff5a26;
    color: #fff;
    padding: 20px 40px;
    text-decoration: none;
    font-size: 1.1em;
    margin-top: 20px;
    letter-spacing: 2px;
}

.searchBox {
    position: absolute;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    background: #05445E;
    padding: 30px 50px;
    transform: translateY(-35%);
    box-shadow: 0 5px 25px rgba(0, 0, 0, 0.1);
}

.searchBox .inputBx {
    position: relative;
    padding: 3px;
}

.searchBox .inputBx input {
    border: 1px solid rgba(24, 154, 180, 0.5);
    background: rgba(5, 68, 94, 0.25);
    border-radius: 1px;
    padding: 10px;
    outline: none;
    color: #fff;
    font-size: 1em;
    transition: 0.5s;
}

.searchBox .inputBx span {
    position: absolute;
    left: 25px;
    padding: 10px;
    pointer-events: none;
    font-size: 1em;
    color: rgba(24, 154, 180, 0.5);
    text-transform: uppercase;
    transition: 0.5s;
}

.searchBox .inputBx ion-icon {
    position: absolute;
    left: 0;
    padding: 10px;
    pointer-events: none;
    color: rgba(255, 255, 255, 0.4);
    font-size: 1.25em;
}

.searchBox .inputBx input:valid~span,
.searchBox .inputBx input:focus~span {
    color: #75E6DA;
    transform: translateX(10px) translateY(-7px);
    font-size: 0.65em;
    padding: 0 10px;
    background: #05445E;
}

.searchBox .inputBx input:valid,
.searchBox .inputBx input:focus {
    border: 1px solid #189AB4;
}

.menuToggle {
    display: none;
}

@media (max-width: 991px) {

    .menuToggle {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 2em;
        cursor: pointer;
        margin-left: 10px;
        color: #05445E;
        width: 30px;
        height: 30px;
        z-index: 1000;
    }

    .menuToggle.active {
        position: fixed;
        top: 20px;
        right: 20px;
    }

    .menuToggle ion-icon {
        position: absolute;
        --ionicon-stroke-width: 20px;
        display: none;
    }

    .menuToggle.active ion-icon:nth-child(1) {
        display: none;
    }

    .menuToggle.active ion-icon:nth-child(2) {
        display: block;
    }

    .menuToggle ion-icon:nth-child(1) {
        display: block;
    }

    .menuToggle ion-icon:nth-child(2) {
        display: none;
    }

    .group .navigation {
        display: none;
    }

    .group .navigation.active {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        background: #fff;
        z-index: 999;
    }

    .group ul.navigation li a {
        font-size: 1.5em;
        margin-left: 0;
        margin: 10px 0;
        display: inline-block;
    }

    .group ul li a {
        margin-left: 20px;
    }

    header {
        padding: 20px;
    }

    .banner {
        padding-right: 10px;
        padding-left: 10px;
        transform:translateY(100px);
    }

    .searchBox {
        padding: 20px;
        width: 80vw;
        transform: translateY(-50%);
    }
}

@media (max-width: 768px) {}